export enum Page {
  'profile' = 'profile',
  'products' = 'products',
  'categories' = 'categories',
}

export interface AlertMessage {
  severity: 'success' | 'error';
  message: string;
  id: string;
}

export interface User {
  email: string;
}

export interface Category {
  id: string;
  name: string;
  photo?: string;
  createdAt: Date;
  updatedAt: Date;
}

export interface Product {
  id: string;
  name: string;
  photo?: string;
  desc?: string;
  createdAt: Date;
  updatedAt: Date;
  oldPrice?: number;
  price: number;
  category: Category;
}

export type CheckoutOrderProduct = Product & { quantity: number };

export interface CheckoutOrder {
  products: Array<CheckoutOrderProduct>;
}

export enum OrderStatus {
  PendingConfirmation = 'pending_confirmation',
  Processing = 'processing',
  Packaging = 'packaging',
  WaitingForDelivery = 'waiting_for_delivery',
  InTransit = 'in_transit',
  Delivered = 'delivered',
  ReturnRequested = 'return_requested',
  OrderCancelled = 'order_cancelled',
}

export interface Profile {
  id: string;
  name: string;
  email: string;
  signUpDate: Date;
}

export interface Pagination {
  pageNumber: number;
  pageSize: number;
  total: number;
}

export enum SortDirection {
  'ASC' = 'ASC',
  'DESC' = 'DESC',
}

export interface Sorting<Field> {
  type: SortDirection;
  field: Field;
}

export interface ServerError {
  errors: Array<{
    extensions: {
      code: ErrorCode;
    };

    name: string;
    fieldName?: string;
    stack: string;
    message: string;
  }>;
}

enum ErrorCode {
  ERR_INCORRECT_EMAIL_OR_PASSWORD = 'ERR_INCORRECT_EMAIL_OR_PASSWORD', //    email  
  ERR_ACCOUNT_ALREADY_EXIST = 'ERR_ACCOUNT_ALREADY_EXIST', //      
  ERR_FIELD_REQUIRED = 'ERR_FIELD_REQUIRED', //  .      fieldName  ,    
  ERR_INCORRECT_PASSWORD = 'ERR_INCORRECT_PASSWORD', //       
  ERR_INVALID_PASSWORD = 'ERR_INVALID_PASSWORD', //      /^[\w-@{}()#$%^&*+=!~]{8,}$/
  ERR_NOT_VALID = 'ERR_NOT_VALID', //   id 
  ERR_AUTH = 'ERR_AUTH', //   ,    
  ERR_NO_FILES = 'ERR_NO_FILES', //    
  ERR_NOT_ALLOWED = 'ERR_NOT_ALLOWED', //      (    )
  ERR_NOT_FOUND = 'ERR_NOT_FOUND', //   
  ERR_VALIDATION_ERROR = 'ERR_VALIDATION_ERROR', //   , ,   name
  ERR_INVALID_QUERY_PARAMS = 'ERR_INVALID_QUERY_PARAMS', //  GET       search params   { [key: string]: string //   JSON.stringify() }
  ERR_INTERNAL_SERVER = 'ERR_INTERNAL_SERVER', //  .   ,     
}
